import React, {Fragment} from 'react';
import {setChangeDealModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Switch from "@material-ui/core/Switch/Switch";

import {IconPack} from "../../../Images/IconPack";

const ChangeDeal = (props) => {

    const [value1, setValue1] = React.useState();
    const [value2, setValue2] = React.useState(true);
    const [title, setTitle] = React.useState('Questions about buying a license');
    const [status, setStatus] = React.useState('Plan to take over the universe');
    const [creationDate, setCreationDate] = React.useState('');
    const [seller, setSeller] = React.useState('');
    const [buyer, setBuyer] = React.useState('');
    const [amountToSend, setAmountToSend] = React.useState(1000);
    const [amountComisionRate, setAmountComisionRate] = React.useState(100);
    const [amountToRecive, setAmountToRecive] = React.useState(1100);
    const [dealId, setDealId] = React.useState('');
    const [dateCreated, setDateCreated] = React.useState('');
    const [isBuyer, setIsBuyer] = React.useState(true);


    let component = '';

    if(props.appState.modals.changeDeal.bool) {

        component = ( <div className="popup-layer">
            <div className="popup-content popup-description w500">
                <header>
                    <h3>Deal details</h3>
                    <div className="close" onClick={()=>{props.setChangeDealModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>

                    <div className="alertPanel">{IconPack.userIcon} Lionel Messi offers to join to Corporate license</div>

                        <div className="row1">
                            <TextField
                                variant="filled"
                                label="Buyer Login"
                                value={props.appState.modals.changeDeal.data.buyer}
                                disabled
                            />
                            <TextField
                                variant="filled"
                                label="Seller Login"
                                value={props.appState.modals.changeDeal.data.seller}
                                disabled
                            />
                        </div>
                        <div className="row2">
                            <TextField
                                variant="filled"
                                label="Amount to send"
                                value={props.appState.modals.changeDeal.data.amountToSend}
                                disabled
                            />
                            <TextField
                                variant="filled"
                                disabled
                                label="Comision rate"
                                value={amountComisionRate}

                            />
                            <TextField
                                variant="filled"
                                disabled
                                label="Amount to recive"
                                value={props.appState.modals.changeDeal.data.amountToRecive}
                            />
                        </div>
                        <div className="row3">
                            <TextField
                                label="Deal title"
                                fullWidth
                                rows={1}
                                disabled
                                value={props.appState.modals.changeDeal.data.title}
                                variant="filled"
                            />
                            <TextField
                                label="Terms of transaction"
                                fullWidth
                                rows={11}
                                disabled
                                multiline
                                value={props.appState.modals.changeDeal.data.description}
                                className={'height-auto'}
                                variant="filled"
                            />
                        </div>

                    <div className="btn btn--blue" onClick={()=>{props.setChangeDealModal(false);}}>Cancel a deal</div>
                </main>

            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChangeDealModal: (obj) => dispatch(setChangeDealModal(obj)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChangeDeal);