import React, {Fragment} from 'react';

import {setSelectForUsers} from "../../../redux/licenses/corporate/users/users.actions";
import {setAuthorizationDataModal} from "../../../redux/app/app.actions";


import {connect} from "react-redux";
import Button from "@material-ui/core/Button";
import {IconPack} from "../../../Images/IconPack";
import TextField from "@material-ui/core/TextField/TextField";



const AuthorizationData = (props) => {


    const applyHandler = () => {
        navigator.clipboard.writeText(newDataObjString);
        props.setSelectForUsers(false);
        props.setAuthorizationDataModal(false);
    };
    let newDataObj = [];

    props.usersTabData.map((i) => {
        if(i.userOptions.isSelected) {
            newDataObj.push(i);
        };
    });

    newDataObj = newDataObj.map((i) => {
        return (i.user.name +': ' + i.user.id + '; ' + i.user.creds.password + ';' )
    });

    let newDataObjString = newDataObj.toString().replace(/,/g, `\r\n`);

    const [value, setValue] = React.useState(newDataObj);


    return (<div className="popup-layer">
        <div className="popup-content w400">
            <header>
                <h3>Authorization data</h3>
                <div className="close" onClick={() => {
                    props.setAuthorizationDataModal(false)
                }}>
                    <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                </div>
            </header>
            <main>
                <TextField
                    label={'Nickname: Login; Password;'}
                    fullWidth
                    multiline
                    disabled
                    rows={newDataObj.length}
                    value={newDataObjString}
                    className='heightAuto'
                    variant="filled"
                />
                <Button variant="contained" className="btn btn--blue"  onClick={()=>{applyHandler()}}>
                    Copy to clipboard
                </Button>

            </main>
        </div>
    </div>);
};


const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setAuthorizationDataModal: (bool) => dispatch(setAuthorizationDataModal(bool)),
        setSelectForUsers: (bool) => dispatch(setSelectForUsers(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AuthorizationData);