import React, {Fragment} from 'react';
import {setRefillLicenseModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";



const RefillLicenseBalance = (props) => {

    let component = null;

    const clickHandler = ( newLangVal ,e ) => {
        props.setCurrentLanguage(newLangVal);
        props.setLanguageModal(false);

    };
    const [value1, setValue1] = React.useState(1500);
    const [value2, setValue2] = React.useState('From account balance');

    const handleChange1 = (event, newValue) => {
        setValue1(newValue);
    };
    const handleChange3 = (event, newValue) => {
        setValue1(newValue);
    };

    const handleChange2 = (event) => {
        setValue2(event.value);
    };


    if(props.appState.modals.refillLicenseBalance) {
        component = ( <div className="popup-layer">
            <div className="popup-content w320">
                <header>
                    <h3>Refill account balance</h3>
                    <div className="close" onClick={()=>{props.setRefillLicenseModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <div className="header">
                        <div>
                            <h6>Account balance: </h6>
                            <p>$ <span className="color-white">3500</span>.00</p>
                        </div>
                        <div>
                            <h6>License balance: </h6>
                            <p>$ <span className="color-white">1063</span>.00</p>
                        </div>
                    </div>
                    <TextField
                        variant="filled"
                        fullWidth
                        select
                        label="Payment method"
                        value={value2}
                        onChange={(e)=>{handleChange2(e.target)}}
                    >
                        <MenuItem value='From account balance'>From account balance</MenuItem>
                        <MenuItem value='From account balance2'>From account balance2</MenuItem>

                    </TextField>
                    <TextField
                        label="Amount to replenish"
                        fullWidth
                        rows={1}
                        value={value1}
                        onChange={handleChange3}
                        variant="filled"
                        className="inputDollar"
                    />
                    <Slider
                        step={100}
                        min={0}
                        max={10000}
                        value={value1} onChange={handleChange1} />
                    <p>Funds will be transferred from account balance to license balance</p>
                    <Button variant="contained" className="btn btn--blue" >
                        GO TO PAYMENT
                    </Button>
                </main>
            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setRefillLicenseModal: (bool) => dispatch(setRefillLicenseModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RefillLicenseBalance);