import React, {useEffect} from 'react';

import LangDropDown from './LangDropdown/LangDropdown';
import BuyPersLicense from './buyPersLicense/buyPersLicense';
import BuyCorpLicense from './buyCorpLicense/buyCorpLicense';
import RefillAccountBalance from './refillAccountBalance/refillAccountBalance';
import RefillLicenseBalance from './refillLicenseBalance/refillLicenseBalance';
import LicenseDaysSetting from './licenseDaysSetting/licenseDaysSetting';
import ChangeEmail from './changeEmail/ChangeEmail';
import ChangePassword from './ChangePassword/ChangePassword';
import ChangeTelegram from './changeTelegram/changeTelegram';
import ChangeDeal from './changeDeal/changeDeal';
import AutoRenewalSettings from './autoRenewalSettings/autoRenewalSettings';
import MigrationToCorporate from './migrationToCorporate/migrationToCorporate';
import SetAccessRights from './setAccessRights/setAccessRights';
import SetAccessRightsForOneUser from './setAccessRightsForOneUser/setAccessRightsForOneUser';
import AuthorizationData from './authorizationData/authorizationData';
import AuthorizationDataForOneUser from './authorizationDataForOneUser/authorizationDataForOneUser';
import {connect} from "react-redux";



const ModalWindow = (props) => {

    let currentWindow = null;

    if(props.appState.modals.languageDropdown)     { currentWindow = <LangDropDown /> };
    if(props.appState.modals.buyCorpLicense.bool)       { currentWindow = <BuyCorpLicense /> };
    if(props.appState.modals.buyPersLicense.bool)       { currentWindow = <BuyPersLicense /> };
    if(props.appState.modals.refillAccountBalance) { currentWindow = <RefillAccountBalance /> };
    if(props.appState.modals.refillLicenseBalance) { currentWindow = <RefillLicenseBalance /> };
    if(props.appState.modals.changeEmail)          { currentWindow = <ChangeEmail /> };
    if(props.appState.modals.changePass)           { currentWindow = <ChangePassword /> };
    if(props.appState.modals.changeTelegram)       { currentWindow = <ChangeTelegram /> };
    if(props.appState.modals.autoRenewal)          { currentWindow = <AutoRenewalSettings /> };
    if(props.appState.modals.migration)            { currentWindow = <MigrationToCorporate /> };
    if(props.appState.modals.accessRights)         { currentWindow = <SetAccessRights /> };
    if(props.appState.modals.accessRightsForOneUser.bool)         { currentWindow = <SetAccessRightsForOneUser /> };
    if(props.appState.modals.licenseDaysSettings.bool)  { currentWindow = <LicenseDaysSetting /> };
    if(props.appState.modals.authorizationData)    { currentWindow = <AuthorizationData /> };
    if(props.appState.modals.authorizationDataForOneUser.bool)    { currentWindow = <AuthorizationDataForOneUser /> };
    if(props.appState.modals.changeDeal.bool)    { currentWindow = <ChangeDeal /> };

    return currentWindow;
};


const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

export default connect(mapStateToProps)(ModalWindow);