import React, {Fragment} from 'react';
import {setAutoRenewalModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Switch from "@material-ui/core/Switch/Switch";



const AutoRenewalSettings = (props) => {

    let component = '';

    const clickHandler = ( newLangVal ,e ) => {
        props.setCurrentLanguage(newLangVal);
        props.setLanguageModal(false);

    };
    const [value1, setValue1] = React.useState(200);
    const [value2, setValue2] = React.useState(500);
    const [value3, setValue3] = React.useState(true);
    const handleChange2 = (event, newValue) => {
        setValue2(newValue);
    };

    if(props.appState.modals.autoRenewal) {
        component = ( <div className="popup-layer">
            <div className="popup-content w320">
                <header>
                    <h3>Auto renewal settings</h3>
                    <div className="close" onClick={()=>{props.setAutoRenewalModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <div className="switch-wrapper">
                        <Switch checked={ value3 }
                                name="autoRenew"
                                onChange={(e)=>{ setValue3(!value3)}} />
                        <p><span className="color-white">Auto renew</span></p>
                    </div>
                    <TextField
                        variant="filled"
                        fullWidth
                        label="License balance left"
                        value={value1}
                        className="inputDollar"
                        onChange={(e)=>{ setValue1(e.target.value)}}
                    >

                    </TextField>
                    <TextField
                        label="Amount to replenish"
                        fullWidth
                        rows={1}
                        value={value2}
                        onChange={(e)=>{ setValue2(e.target.value)}}
                        variant="filled"
                        className="inputDollar"
                    />
                    <Slider
                        step={100}
                        min={0}
                        max={10000}
                        value={value2} onChange={handleChange2} />
                    <p>When the balance of the license is less than <span className="color-white">${value1}</span>, it will automatically replenish by <span className="color-white">${value2}</span>  from account balance</p>
                    <div className="btn btn--blue">GO TO PAYMENT</div>

                </main>
            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setAutoRenewalModal: (bool) => dispatch(setAutoRenewalModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AutoRenewalSettings);