import React, {Fragment , useEffect} from 'react';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Slider from "@material-ui/core/Slider";
import {setAccessRightsForOneUserModal} from "../../../redux/app/app.actions";
import {setAccessRights} from "../../../redux/licenses/corporate/users/users.actions";

import {connect} from "react-redux";
import Button from "@material-ui/core/Button";
import {IconPack} from "../../../Images/IconPack";
import Switch from "@material-ui/core/Switch/Switch";
import TableCell from "@material-ui/core/TableCell/TableCell";


const SetAccessRightsForOneUser = (props) => {
    let component = null;

    let currentUserObj =  props.usersTabData.find((userObject) => {
        return userObject.user.id === props.appState.modals.accessRightsForOneUser.data;
    });

    const [seesActivity, setSeesActivity] = React.useState();
    const [seesUsers, setSeesUsers] = React.useState();
    const [seesCosts, setSeesCosts] = React.useState();

    useEffect(() => {
        setSeesActivity( currentUserObj.userOptions.seesActivity );
        setSeesUsers( currentUserObj.userOptions.seesUsers );
        setSeesCosts(currentUserObj.userOptions.seesCosts);
    }, []);



    const applyHandler = () => {

        let newDataObj = props.usersTabData.map((i, key) => {
            if(i.user.id === props.appState.modals.accessRightsForOneUser.data) {
                i.userOptions.seesActivity = seesActivity;
                i.userOptions.seesUsers = seesUsers;
                i.userOptions.seesCosts = seesCosts;
            };
            return i
        })

        props.setAccessRights(newDataObj);
        props.setAccessRightsForOneUserModal({bool: false, data: null})
    };



    return (<div className="popup-layer">
        <div className="popup-content w320">
            <header>
                <h3>Set access rights</h3>
                <div className="close" onClick={() => {
                    props.setAccessRightsForOneUserModal({bool: false, data: null})
                }}>
                    <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                </div>
            </header>
            <main>
                <div className="header">
                    <div>
                        <h6>User: </h6>
                        <p className="icon-inside">{IconPack.userIcons.forTable} <b>{currentUserObj.user.name}</b> </p>
                    </div>
                </div>
                <div className="line mb-10"></div>
                <div><Switch
                    checked={seesActivity}
                    onChange={() => {
                        setSeesActivity(!seesActivity)
                    }}
                /><span className="color-white ml-10">Sees activity</span></div>
                <div><Switch
                    checked={seesUsers}
                    onChange={() => {
                        setSeesUsers(!seesUsers)
                    }}
                /><span className="color-white ml-10">Sees users</span></div>
                <div><Switch
                    checked={seesCosts}
                    onChange={() => {
                        setSeesCosts(!seesCosts)
                    }}
                /><span className="color-white ml-10">Sees costs</span></div>


                <p className="mt-10">Access rights will be applied to {currentUserObj.user.name}</p>

                <Button variant="contained" className="btn btn--blue"  onClick={()=>{applyHandler()}}>
                    Save changes
                </Button>

            </main>
        </div>
    </div>);
};


const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setAccessRightsForOneUserModal: (bool) => dispatch(setAccessRightsForOneUserModal(bool)),
        setAccessRights: (obj) => dispatch(setAccessRights(obj)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SetAccessRightsForOneUser);