import React, {Fragment} from 'react';
import {setChangeTelegramModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Switch from "@material-ui/core/Switch/Switch";



const ChangeTelegram = (props) => {

    const [value1, setValue1] = React.useState();
    const [value2, setValue2] = React.useState(true);

    let component = '';

    if(props.appState.modals.changeTelegram) {
        component = ( <div className="popup-layer">
            <div className="popup-content w320">
                <header>
                    <h3>Change Telegram</h3>
                    <div className="close" onClick={()=>{props.setChangeTelegramModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <TextField
                        variant="filled"
                        fullWidth
                        label="Your telegram"
                        value={value1}
                        onChange={(e)=>{ setValue1(e.target.value)}}>
                    </TextField>

                    <div className="switch-wrapper">
                        <Switch checked={ value2 }
                                name="autoRenew"
                                onChange={(e)=>{ setValue2(!value2)}} />
                        <p><span className="color-white">Display for other users</span></p>
                    </div>
                    <div className="btn btn--blue" onClick={()=>{props.setChangeTelegramModal(false)}}>Save changes</div>
                </main>

            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChangeTelegramModal: (bool) => dispatch(setChangeTelegramModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChangeTelegram);