import { usersTypes} from './users.types'


export const setActivity = ( bool, id ) => ({
  type: usersTypes.SET_ACTIVITY,
  payload: {bool, id}
})

export const setUsers = ( bool, id ) => ({
  type: usersTypes.SET_USERS,
  payload: {bool, id}
})

export const setCosts = ( bool, id ) => ({
  type: usersTypes.SET_COSTS,
  payload: {bool, id}
})

export const setDropdown = ( bool, id ) => ({
  type: usersTypes.SET_DROPDOWN,
  payload: {bool, id}
})

export const setSelect = ( bool, id ) => ({
  type: usersTypes.SET_SELECT,
  payload: {bool, id}
})

export const sortTableByType = ( type ) => ({
  type: usersTypes.SORT_TABLE_BY_TYPE,
  payload: type
})

export const setAccessRights = ( obj ) => ({
  type: usersTypes.SET_ACCESS_RIGHTS,
  payload: obj
})

export const setSelectForUsers = ( bool ) => ({
  type: usersTypes.SET_SELECT_FOR_USERS,
  payload: bool
})

export const updateUsers = ( obj ) => ({
  type: usersTypes.UPDATE_USERS,
  payload: obj
})

export const setDaysLeft = ( obj ) => ({
  type: usersTypes.SET_DAYS_LEFT,
  payload: obj
})


export const setConfigLimit = ( obj ) => ({
  type: usersTypes.SET_CONFIG_LIMIT,
  payload: obj
})