import React, {Fragment} from 'react';
import {setBuyPLicenseModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import { useHistory } from "react-router-dom";
import Button from "@material-ui/core/Button";


const BuyPersLicense = (props) => {

    let component = '';

    let history = useHistory();

    const clickHandler = ( newLangVal ,e ) => {
        props.setCurrentLanguage(newLangVal);
        props.setLanguageModal(false);

    };
    const [value1, setValue1] = React.useState(props.appState.modals.buyPersLicense.data);
    const [value2, setValue2] = React.useState('From account balance');

    const handleChange1 = (event, newValue) => {
        if(newValue >= 1000) {
            setValue1(newValue);
        }
    };
    const handleChange3 = (event, newValue) => {

            setValue1(newValue);

    };
    const handleChange2 = (event) => {
        setValue2(event.value);
    };



    if(props.appState.modals.buyPersLicense) {
        component = ( <div className="popup-layer">
            <div className="popup-content w320">
                <header>
                    <h3>Buy Personal License</h3>
                    <div className="close" onClick={()=>{props.setBuyPLicenseModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <TextField
                        variant="filled"
                        fullWidth
                        select
                        label="Payment method"
                        value={value2}
                        onChange={(e)=>{handleChange2(e.target)}}
                    >
                        <MenuItem value='From account balance'>From account balance</MenuItem>
                        <MenuItem value='From Bitcoin wallet'>From Bitcoin wallet</MenuItem>

                    </TextField>
                    <TextField
                        label="Amount"
                        fullWidth
                        rows={1}
                        value={value1}
                        onChange={handleChange3}
                        variant="filled"
                        className="inputDollar"
                    />
                    <Slider
                        step={100}
                        min={0}
                        max={10000}
                        value={value1} onChange={handleChange1} />
                <p>Funds will be transferred from account balance to license balance</p>

                    <Button variant="contained" className="btn btn--blue" onClick={()=>{history.push("/licenses/1234567890/activity");props.setBuyPLicenseModal(false) }}>
                        GO TO PAYMENT
                    </Button>
                </main>
            </div>
        </div>);
    }

    return component;
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setBuyPLicenseModal: (bool) => dispatch(setBuyPLicenseModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BuyPersLicense);