import React, {Fragment} from 'react';
import {setLicenseDaysSettingsModal} from "../../../redux/app/app.actions";
import {setDaysLeft} from "../../../redux/licenses/corporate/users/users.actions";

import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import {IconPack} from "../../../Images/IconPack";
import Button from "@material-ui/core/Button";


const LicenseDaysSetting = (props) => {

    let component = null;

    let defaultDescription = 'Set a new value for the remaining days. You can edit any field, the value of the remaining fields will change automatically.';

    const currentData =  Object.assign({}, props.appState.modals.licenseDaysSettings.data );
    const [balance, setBalance] = React.useState('$' + currentData.userOptions.balance);
    const [spend, setSpendValue] = React.useState(0);
    const [spendClass, setSpendClass] = React.useState('');
    const [daysValue, setDaysValue] = React.useState(currentData.userOptions.daysLeft);

    let configRate = 2;


    const daysHandler = (value) => {
        setDaysValue(value);

        if(value > currentData.userOptions.daysLeft) {
            setBalance('$' + (currentData.userOptions.balance + (value - currentData.userOptions.daysLeft)*configRate).toFixed(2) );

            setSpendValue('-$' +((value - currentData.userOptions.daysLeft)*configRate).toFixed(2));
            setSpendClass('color-red');

        }else if(value === currentData.userOptions.daysLeft){
            setSpendClass('');
            setSpendValue(0);
            setBalance('$' +currentData.userOptions.balance);
        }else {
            setSpendClass('color-green');
            setSpendValue('+$' + ((currentData.userOptions.daysLeft - value)*configRate).toFixed(2));
            setBalance('$' +(currentData.userOptions.balance + (value - currentData.userOptions.daysLeft)*configRate).toFixed(2) );
        }


    };


    const sliderHandler = (event, newValue) => {
        setDaysValue(newValue);

        if(newValue > currentData.userOptions.daysLeft) {
            setBalance('$' + (currentData.userOptions.balance + (newValue - currentData.userOptions.daysLeft)*configRate).toFixed(2) );

            setSpendValue('-$' +((newValue - currentData.userOptions.daysLeft)*configRate).toFixed(2));
            setSpendClass('color-red');

        }else if(newValue === currentData.userOptions.daysLeft){
            setSpendClass('');
            setSpendValue(0);
            setBalance('$' +currentData.userOptions.balance);
        }else {
            setSpendClass('color-green');
            setSpendValue('+$' + ((currentData.userOptions.daysLeft - newValue)*configRate).toFixed(2));
            setBalance('$' +(currentData.userOptions.balance + (newValue - currentData.userOptions.daysLeft)*configRate).toFixed(2) );
        }

    };

    const applyHandler = () => {

        let data = {
            id: currentData.user.id,
            balance: +balance.match(/\d+/)[0],
            daysValue: daysValue,
        }
        props.setDaysLeft(data);
        setSpendValue(0);
        setSpendClass('');
        props.setLicenseDaysSettingsModal(false)
    };



    component = ( <div className="popup-layer">
            <div className="popup-content w400">
                <header>
                    <h3>Days Settings</h3>
                    <div className="close" onClick={()=>{props.setLicenseDaysSettingsModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <div className="header">
                        <div>
                            <h6>User: </h6>
                            <p>
                            <span className="icon-inside">{IconPack.userIcons.forTable} {currentData.user.name}</span>
                            </p>

                        </div>
                        <div>
                            <h6>Days left: </h6>
                            <p className="color-white">{currentData.userOptions.daysLeft}</p>
                        </div>
                    </div>

                    <div className="line"></div>

                    <div className="fieldsWrapper">
                    <TextField
                        label="Days left"
                        fullWidth
                        rows={1}
                        value={daysValue}
                        onChange={(e) => {daysHandler(e.target.value)}}
                        variant="filled"
                        className="col-3"
                    />
                    <TextField
                        label="Balance"
                        fullWidth
                        rows={1}
                        value={balance}
                        variant="filled"
                        className="col-3"

                    />
                    <TextField
                        label={daysValue < currentData.userOptions.daysLeft ?   'Refund' : 'Spend'}
                        fullWidth
                        rows={1}
                        value={spend}
                        variant="filled"
                        className={spendClass + " col-3"}
                    /></div>

                    <Slider
                        step={1}
                        min={0}
                        max={90}
                        value={daysValue} onChange={sliderHandler} />
                    <p>
                        {daysValue > currentData.userOptions.daysLeft
                            ? (<>You are about to <b className="color-white">add {daysValue - currentData.userOptions.daysLeft} days.</b> The cost of adding is <span className="color-red">{spend}</span>. Funds will be deducted from the balance of the license.</>)
                            : null}
                        {daysValue === currentData.userOptions.daysLeft
                            ? (<>Set a new value for the remaining days. You can edit any field, the value of the remaining fields will change automatically.</>)
                            : null}
                        {daysValue < currentData.userOptions.daysLeft
                            ? (<>You are about to <b className="color-white">take {currentData.userOptions.daysLeft - daysValue} days.</b> The reimbursement cost will be <span className="color-green">{spend}</span>. The funds will be credited to the balance of your license.</>)
                            : null}
                    </p>
                    <Button variant="contained" className="btn btn--blue" onClick={()=>{applyHandler()}}>
                        SAVE CHANGES
                    </Button>
                </main>
            </div>
        </div>);


    return component;
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (bool) => dispatch(setLicenseDaysSettingsModal(bool)),
        setDaysLeft: (obj) => dispatch(setDaysLeft(obj)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LicenseDaysSetting);
