import React, {Fragment} from 'react';
import {setChangeEmailModal, setNewEmailAuthModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";



const ChangeEmail = (props) => {

    let component = '';

    if(props.appState.modals.changeEmail) {
        component = ( <div className="popup-layer">
            <div className="popup-content w320">
                <header>
                    <h3>Change Email</h3>
                    <div className="close" onClick={()=>{props.setChangeEmailModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <p>
                        Email change is out after confirmation of current e-mail. We will send an email to <span className="color-white">Lionel.Messi@gmail.com</span> which contains a link that you should click on
                    </p>
                    <div className="btn btn--blue" onClick={()=>{props.setChangeEmailModal(false); props.setNewEmailAuthModal(true)}}>Send link from my email</div>
                </main>
            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setChangeEmailModal: (bool) => dispatch(setChangeEmailModal(bool)),
        setNewEmailAuthModal: (bool) => dispatch(setNewEmailAuthModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ChangeEmail);