import React, {Fragment} from 'react';
import {setMigrationModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Switch from "@material-ui/core/Switch/Switch";



const MigrationToCorporate = (props) => {

    let component = '';

    const clickHandler = ( newLangVal ,e ) => {
        props.setCurrentLanguage(newLangVal);
        props.setLanguageModal(false);

    };
    const [value1, setValue1] = React.useState(200);
    const [value2, setValue2] = React.useState(500);
    const [value3, setValue3] = React.useState(true);
    const handleChange2 = (event, newValue) => {
        setValue2(newValue);
    };

    if(props.appState.modals.migration) {
        component = ( <div className="popup-layer">
            <div className="popup-content w500">
                <header>
                    <h3>Migration to corporate</h3>
                    <div className="close" onClick={()=>{props.setMigrationModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>

                    <div className="alertPanel">Lionel Messi offers to join to Corporate license</div>

                    <span className="color-orange"><svg width="17" height="15" className="mr-10" viewBox="0 0 17 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M16.5586 12.8086L9.99609 1.40625C9.50391 0.558594 8.21875 0.53125 7.72656 1.40625L1.16406 12.8086C0.671875 13.6562 1.30078 14.75 2.3125 14.75H15.4102C16.4219 14.75 17.0508 13.6836 16.5586 12.8086ZM8.875 10.4297C9.55859 10.4297 10.1328 11.0039 10.1328 11.6875C10.1328 12.3984 9.55859 12.9453 8.875 12.9453C8.16406 12.9453 7.61719 12.3984 7.61719 11.6875C7.61719 11.0039 8.16406 10.4297 8.875 10.4297ZM7.67188 5.91797C7.64453 5.72656 7.80859 5.5625 8 5.5625H9.72266C9.91406 5.5625 10.0781 5.72656 10.0508 5.91797L9.85938 9.63672C9.83203 9.82812 9.69531 9.9375 9.53125 9.9375H8.19141C8.02734 9.9375 7.89062 9.82812 7.86328 9.63672L7.67188 5.91797Z" fill="#F2994A"/>
                    </svg>
                    If you accept his invitation, the following will happen:</span>

                    <ol>
                        <li>Your sessions and configs will be available to users of the corporate license of Ronaldo</li>
                        <li>The balance of your balance will go in favor of the balance of the corporate license of Ronaldo</li>
                        <li>Your account with the email Lionel.Messi@gmail.com and login Lionel Messi will be permanently deleted, but you can register an empty account again</li>
                    </ol>

                    <p className="description">If you dont know what this is about, click the REJECT button and the user will not disturb you anymore.</p>

                    <div className="switch-wrapper">
                        <Switch checked={ value3 }
                                name="autoRenew"
                                onChange={(e)=>{ setValue3(!value3)}} />
                        <p><span className="color-white">Everything is clear, I want to migrate to corporate</span></p>
                    </div>

                    <div className="btns-wrapper">
                        <div className="btn btn--decline">Reject invite</div>
                        <div className="btn btn--accept">Decline invite</div>
                    </div>


                </main>
            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setMigrationModal: (bool) => dispatch(setMigrationModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MigrationToCorporate);